import { Component } from "react";
import { Link } from 'react-router-dom'

import './main.scss'

import OverviewSmallBlocks from '../components/OverviewSmallBlocks/OvieviewSmallBlocks';
import OverviewBigBlock from '../components/OveviewBigBlock/OverviewBigBlock'


class Overview extends Component {

    constructor() {
        super();
    }
    render() {

        return (
            <div className="overview">
                <div className="row1">
                    <OverviewSmallBlocks numSpan='Unresolved' numH2='60' />
                    <OverviewSmallBlocks numSpan='Overdue' numH2='16' />
                    <OverviewSmallBlocks numSpan='Open' numH2='43' />
                    <OverviewSmallBlocks numSpan='On hold' numH2='64' />
                </div>

                <div className="row2">
                    <OverviewBigBlock/>
                </div>
            </div>
        )
    }
}

export default Overview;