import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom';

import { Component, match }  from 'react';

import './assets/styles/main.scss';

import LeftBox from './containers/LeftBox/LeftBox';

import Header from './containers/Header/Header';



import {
  Agents,
  Articles,
  Contacts,
  Ideas,
  Overview,
  Settings,
  Subscription,
  Tickets
} from './pages/index';

class App extends Component {

  constructor() {
    super();
    this.state = {
      activePage: ''
    }
  }

  setActivePage = (page) => {
    this.setState({
      activePage: page
    })
  }

  


  componentDidMount () {
    let activeingPage = `${window.location.pathname}`;
    let abc = activeingPage.split('/');
    this.setActivePage(abc)
  }
  

  render() {

    

    
    
    return(
      
        
        <div className="appbox">
          <Router>
            <div className="bodyLeft">
              <LeftBox activePage={this.state.activePage} setActivePage={this.setActivePage}/>
              
            </div>

            <div className="bodyRight">
            <Header activePage={this.state.activePage} />
            <Switch>
              <Route exact path="/Overview" component={Overview} />
              <Route exact path="/Tickets" component={Tickets} />
              <Route exact path="/Ideas" component={Ideas} />
              <Route exact path="/Contacts" component={Contacts} />
              <Route exact path="/Agents" component={Agents} />
              <Route exact path="/Articles" component={Articles} />
              <Route exact path="/Settings" component={Settings} />
              <Route exact path="/Subscription" component={Subscription} />
            </Switch>
            </div>
            
          </Router>
        </div>
     
      
    )
  }
}

export default App
