import { Component } from "react";



import './main.scss';



class LeftBoxBtn extends Component {

    

    constructor() {
        super();
        
    }

    render() {
        const {title, icon, onClick} = this.props

        return (
            <button className={`sidebar-btn`} onClick={onClick}>
                <div className="sidebarIcon">{icon}</div>
                <span>{title}</span>
            </button>

            
            
        )
    }
}
export default LeftBoxBtn;