import { Component } from "react";

import { Link } from 'react-router-dom'


class IdeasBlock extends Component {

    render() {

        const { ideasImg, ideasTitle, ideasInfo, ideasLink } = this.props
        return(
            <div className="ideasBlock">
                <div className="ideasBlock-img">
                    <img src={ideasImg} alt=""/>
                </div>

                <div className="ideasBlock-info">
                    <h3>{ideasTitle}</h3>
                    <p>{ideasInfo}</p>
                    <Link to={ideasLink} className={`ideasBtn`}>More</Link>
                </div>
            </div>
        )
    }
}

export default IdeasBlock;