import { Component } from "react";

import OverviewBigerSmallBtn from '../OverviewBigerSmallBtn/OverviewBigerSmallBtn';

import './main.scss';


class OverviewBigBlock extends Component {


    render() {
        return (
            <div className="overviewBigBlock">
                <div className="overviewBigBlock-left">
                    <div className="overviewBigBlock-left-header">
                        <div className="overviewBigBlock-left-header-row1">
                            <h3>Today's trends</h3>
                        </div>

                        <div className="overviewBigBlock-left-header-row2">
                            <div className="overviewBigBlock-left-header-row2-left">
                                <span>
                                    as of 25 May 2019, 09:41 PM 
                                </span>
                            </div>

                            <div className="overviewBigBlock-left-header-row2-right">
                                <span>Today</span>
                                <span>Yesterday</span>
                            </div>
                        </div>
                    </div>

                    <div className="overviewBigBlock-left-body">

                    </div>
                </div>

                <div className="overviewBigBlock-right">
                    <OverviewBigerSmallBtn numSpan='Resolved'  numH2='449'/>
                    <OverviewBigerSmallBtn numSpan='Received' numH2='426'/>
                    <OverviewBigerSmallBtn numSpan='Average first response time' numH2='33m'/>
                    <OverviewBigerSmallBtn numSpan='Average response time' numH2='3h 8m'/>
                    <OverviewBigerSmallBtn numSpan='Resolution within SLA' numH2='94%'/>
                </div>
            </div>
        )
    }
}

export default OverviewBigBlock;