import { Component } from 'react';
import './main.scss';

class OverviewBigerSmallBtn extends Component {
    constructor() {
        super();
    }

    render() {

        const { numSpan, numH2 } = this.props


        return (
            <div className="overBigSmallBox">
                <span>{numSpan}</span>
                <h2>{numH2}</h2>
            </div>
        )
    }
}

export default OverviewBigerSmallBtn;