import { Component } from 'react';
import './main.scss';

class OverviewSmallBlocks extends Component {
    constructor() {
        super();
    }
    
    render() {

        const {numSpan, numH2} = this.props


        return(
            <div className="overBox">
                <span>{numSpan}</span>
                <h2>{numH2}</h2>
            </div>
        )
    }
}

export default OverviewSmallBlocks;