import { Component } from "react";

import { Link } from 'react-router-dom';

import logo from '../../assets/images/logo.png'

import './LeftBox.scss'

import {
    ChartIcons,
    TicketIcon,
    IdeaIcon,
    PeopleIcon,
    AgentIcon,
    ArticleIcon,
    SettingsIcon,
    SubsIcon
    
 } from '../../assets/icons/icons';

import LeftBoxBtn from '../../components/LeftBoxBtn/LeftBoxBtn';


class LeftBox extends Component {
    render() {

        const { activePage, setActivePage} = this.props;

        return (
            <div className="box">
                <div className="box-header">
                    <img src={logo} alt=""/>
                    <h2>Dashboard Kit</h2>
                </div>
                <Link
                    to="/Overview"
                    className={`btn ${activePage == 'Overview' ? 'active' : ''}`}>
                    <LeftBoxBtn  title={'Overview'} icon={<ChartIcons/>}
                    onClick={() => setActivePage('Overview')}/>
                 </Link>

                <Link
                    to="/Tickets"
                    className={`btn ${activePage == 'Tickets' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Tickets'} icon={<TicketIcon />} 
                    onClick={() => setActivePage('Tickets')}/>
                </Link>


                <Link 
                    to="/Ideas" 
                    className={`btn ${activePage == 'Ideas' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Ideas'} icon={<IdeaIcon />} 
                    onClick={() => setActivePage('Ideas')}/>
                </Link>


                <Link 
                    to="/Contacts" 
                    className={`btn ${activePage == 'Contacts' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Contacts'} icon={<PeopleIcon />} 
                    onClick={() => setActivePage('Contacts')}/>
                </Link>


                <Link 
                    to="/Agents" 
                    className={`btn ${activePage == 'Agents' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Agents'} icon={<AgentIcon />} 
                    onClick={() => setActivePage('Agents')}/>
                </Link>


                <Link 
                    to="/Articles" 
                    className={`btn ${activePage == 'Articles' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Articles'} icon={<ArticleIcon />} 
                    onClick={() => setActivePage('Articles')}/>
                </Link>


                <Link 
                    to="/Settings" 
                    className={`btn ${activePage == 'Settings' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Settings'} icon={<SettingsIcon />} 
                    onClick={() => setActivePage('Settings')}/>
                </Link>


                <Link 
                    to="/Subscription" 
                    className={`btn ${activePage == 'Subscription' ? 'active' : ''}`}>
                    <LeftBoxBtn title={'Subscription'} icon={<SubsIcon />} 
                    onClick={() => setActivePage('Subscription')}/>
                </Link>
            </div>
        )
        
    }
}

export default LeftBox